.AddNote {
  color: #474646;
  margin-left: 20px;
}

.newNote {
  display: flex;
  flex-direction: column;
}

input,
select {
  width: 60%;
  margin: 5px 0;
  border: #474646 solid 1px;
  border-radius: 1px;
  font-size: 14px;
}
textarea {
  width: 80%;
  height: 200px;
  resize: none;
  margin: 5px 0;
  border: #474646 solid 1px;
  border-radius: 1px;
  font-size: 14px;
}
