.addFolder {
  color: #474646;
  margin-left: 20px;
}

h1 {
  margin-top: 0;
}

.newFolder {
  display: flex;
  flex-direction: column;
}

h2 {
  margin: 5px 0;
}

input {
  padding: 8px;
  border-top: 0;
  width: 60%;
  border: #474646 solid 1px;
  border-radius: 1px;
  font-size: 14px;
}

.addFolderButton,
.addNoteButton {
  margin-top: 10px;
  background-color: #d2d1d1;
  border-radius: 3px;
  border: 0;
  color: #474646;
  font-size: 16px;
  font-weight: bold;
  width: 10%;
  padding: 8px;
}

@media (max-width: 600px) {
  .addNoteButton {
    width: 30%;
  }
}

@media (max-width: 800px) {
  .addNoteButton {
    width: 20%;
  }
}
