.App {
  --custom-font: 'Montserrat', sans-serif;
  display: grid;
  grid-template-columns: 1fr 3fr 1fr;
  grid-template-rows: 1fr 0.1fr 7fr;
  min-height: 100vh;
}

.App__nav {
  background-color: white;
  grid-column: 1;
  grid-row: 3;
  overflow: auto;
}

.siteTitle {
  font-family: 'Crimson Text', serif;
  font-size: 3.2em;
  grid-column: 1;
}

.App__header {
  background-color: white;
  align-self: center;
  justify-self: center;
}

.App__header a {
  color: rgb(110, 151, 151);
  font-size: 1.4em;
  text-decoration: none;
}

.App__main {
  background-color: none;
  grid-row-start: 2;
  grid-row-end: 4;
  grid-column: span 2;
  height: calc(100vh - 120px);
  overflow: auto;
}

.NavCircleButton {
  background-color: #d2d1d1;
  border-radius: 3px;
  border: 0;
  color: black;
  font-size: 12px;
  font-weight: bold;
  height: 50px;
  padding: 8px;
  text-align: center;
  text-decoration: none;
  width: 100px;
}

.NoteListMain__button-container {
  margin: 0;
  display: flex;
  flex-direction: row;
  align-self: center;
  justify-self: center;
  grid-column: 1;
  grid-row: 2;
}

a.NavCircleButton {
  padding: 7px 8px 11px;
  color: #474646;
  font-size: 1em;
}

.NavCircleButton:hover {
  cursor: pointer;
  color: white;
  background-color: #acabab;
}

.NoteListMain__add-note-button,
.NoteListNav__add-folder-button {
  margin: 0 4%;
  padding: 0;
}

#text {
  vertical-align: middle;
}

@media (max-width: 600px) {
  .App {
    grid-template-rows: 1fr 4fr;
    grid-template-columns: 1fr 4fr;
    margin-left: 20px;
  }
  .App__main {
    grid-column-start: 1;
    grid-column-end: 3;
  }
  .NoteListMain__button-container {
    grid-column: 2;
    grid-row: 1;
  }
  .hamburger {
    grid-column: 3;
    justify-self: end;
  }
  .fa-bars {
    font-size: 1.4em;
  }
  .logo img {
    display: block;
    height: 70px;
  }
}

@media (max-width: 600px) {
  .App {
    grid-template-rows: 0.5fr 1fr 4fr;
    grid-template-columns: 1fr 4fr;
    margin: 0 20px;
  }
  .siteTitle {
    font-size: 3em;
    margin-left: 35px;
    margin-bottom: 10px;
  }
  .App__nav {
    grid-row: 2;
    grid-column: 1 / -1;
  }
  .App__main {
    grid-row: 3;
  }
  .NoteListMain__button-container {
    justify-self: right;
    margin-top: 10px;
  }
}

@media (max-width: 400px) {
  a.NavCircleButton {
    padding: 5px;
    width: 80px;
    height: 40px;
    font-size: 0.7em;
  }
  .App__main {
    grid-row: 3;
  }
  .NoteListMain__button-container {
    grid-row: 2;
    margin: 10px 30px 20px 10px;
  }
  .App__header {
    grid-column: 1 / -1;
    margin-right: 20px;
  }
  .siteTitle {
    font-size: 2em;
    margin: 10px;
  }
}
